    font-size: 16px;
    font-weight: bold; }
  head:hover {
    background: rgba(255, 255, 255, 0.5);
    color: red; }
  head:hover ~ #wrapper,
  head:hover ~ body #wrapper {
    position: relative; }
    head:hover ~ #wrapper:before,
    head:hover ~ body #wrapper:before {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      content: " ";
      z-index: 998;
      background-image: linear-gradient(to right, rgba(102, 102, 255, 0.15), rgba(179, 179, 255, 0.15) 80%, transparent 80%);
      background-size: 8.47458%;
      background-origin: content-box;
      background-clip: content-box;
      background-position: left top; }

